<template>
    <div page-index>
        <div>
            <BortherVue />
            <SisterVue />
        </div>
        <div v-card>
            <p>全局事件总线(任意组件通信)</p>
            <div>
                <pre>
父传子使用props属性, 子传父使用$emit函数, 那么兄弟之间如何传递数据呢?
拿转账来说, 网络支付没出来之前, 都是通过银行代理操作的(找到银行, 告诉银行你要转账的对象以及转账的金额, 然后银行就会帮你进行转账操作)
在Vue中, 我们可以找一个公共的组件, 由它进行代理操作, 让所有需要传递数据的组件都去找它帮忙
那么, 问题来了, 谁能让所有组件都可以访问到呢?
杠:
  a: Vue的实例对象以及VueComponent都可以被访问到
例1:
  main.js->
  const worker = Vue.extend({})
  // 在Vue原型上添加一个工作者, 让其帮忙做中间商的角色
  Vue.prototype.worker = worker

  other components->
  this.worker.$on('xxx', {})
  this.worker.$emit('xxx', [...args])
  以上是通过公用组件的方式实现的, 但是, 这种方式不推荐, 因为还要创建组件将其绑定到Vue原型上, 还有种不用创建Vue组件的方式
例2: 众所周知, Vue实例对象也可以被访问到, 因此可以在Vue创建之前, 将一个代理工作者(中间商)添加到Vue原型上
  main.js->
  new Vue({
    ...
    beforeCreated() {
        Vue.prototype.$worker = this
    }
    ...
  })

  other components->
  // 接收数据组件
  this.$worker.$on(...)
  // 传递数据组件
  this.$worker.$emit(...)
以上的方式可以在任意组件之间传递数据
注:
  最好在绑定事件的组件上, 在销毁前解绑事件
  beforeDestory() {
    this.$worker.off('...')
  }
                </pre>
            </div>
        </div>
    </div>
</template>

<script>
import BortherVue from '@/common/Borther.vue';
import SisterVue from '@/common/Sister.vue';

export default {
    name: 'Communication.',
    components: { BortherVue, SisterVue },
    data() {
        return {};
    },
};
</script>

