<template>
    <div
        v-shadow="{ radius: 8 }"
        style="margin-top: 1rem">
        <p v-pre>我是妹妹</p>
        <p v-show="money">+{{ money }}</p>
    </div>
</template>

<script>
export default {
    name: 'Sister',
    data() {
        return {
            money: null,
        };
    },
    mounted() {
        this.$worker.$on('transferAccount', (money) => {
            this.money = money;
            console.log(`收到了哥哥的转账, ${money}元`);
            alert(`收到了哥哥的转账, ${money}元`);
        });
    },
    beforeDestroy() {
        this.$worker.$off('transferAccount');
    },
};
</script>

<style scoped></style>
